.proj {
  margin-top: 60px;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: rgb(33, 25, 21);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  z-index: 3;
}

.proj_skill {
  font-size: calc(11px + 2vmin);
  font-weight: 700;
  text-transform: uppercase;
  margin: 40px 0px;
}
.proj_title {
  margin-bottom: 20px;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 600;
  color: rgb(153, 153, 153);
  border-width: 2px;
  border-style: solid;
  border-color: rgb(153, 153, 153);
  border-image: initial;
  border-radius: 5px;
  padding: 10px 15px;
}
.proj_icons {
  display: flex;
  flex-wrap: wrap;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  width: 100%;
  max-width: 935px;
}
.proj_align_icon {
  display: flex;
  flex-direction: column;
  -webkit-box-align: center;
  align-items: center;
  margin-bottom: 30px;
}
.proj_fe_icon {
  width: 100px;
  height: 100px;
  margin-bottom: 5px;
}
.proj_fe_name {
  font-size: 12px;
  margin-bottom: 10px;
}
.proj_skills_list {
  max-width: 935px;
  flex-direction: column;
  position: absoulte;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  height: 100%;
  color: rgb(33, 25, 21);
}
.proj_proj_name {
  text-transform: uppercase;
  font-size: calc(11px + 2vmin);
  text-align: center;
  font-weight: 700;
  margin: 40px 0px;
}

.proj_proj {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.proj_img {
  width: 80%;
  /* max-width: 935px; */
  height: 10%;
  margin-bottom: 20px;
  box-shadow: rgb(136, 136, 136) 5px 10px 18px;
  border-radius: 40px;
}
.slide_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: auto;
  height: 10%;
}

.slide_images {
  width: 100%;
  max-width: 400px;
  max-height: 600px;
  border-radius: 40px;
}

@media screen and (max-width: 768px) {
  .slide_container {
    flex-direction: column;
  }
}

.proj_proj_intro {
  width: 100%;
  max-width: 935px;
  display: flex;
  flex-direction: column;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  margin-bottom: 40px;
}
.proj_proj_text {
  width: 85%;
  max-width: 935px;
  text-align: justify;
  letter-spacing: 2px;
  line-height: 1.5rem;
}

.proj_proj_title {
  width: 100%;
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  align-items: center;
  margin: 20px 0px;
  font-size: calc(9.5px + 2vmin);
  font-weight: 600;
}
.proj_button {
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  color: rgb(153, 153, 153);
  background-color: rgb(250, 250, 250);
  margin-left: 0px;
  text-transform: uppercase;
  margin-right: 0px;
  cursor: pointer;
  outline: none;
  padding: 5px;
  border-width: 2px;
  border-style: solid;
  border-color: rgb(153, 153, 153);
  border-image: initial;
  border-radius: 5px;
  transition: all 0.4s ease-in-out 0s;
}
.proj_text {
  margin-bottom: 15px;
  font-size: calc(9px + 1vmin);
  font-weight: 500;
}
.proj_proj_skills {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}
.skill_name {
  background-color: rgb(153, 153, 153);
  color: white;
  font-size: 12px;
  padding: 5px;
  border-radius: 5px;
  margin: 10px 0px;
  margin-right: 10px;
}

